// goods
import React, { Component } from 'react';
import { Breadcrumb, Icon, Button, Tabs, Radio, Modal, Input, Select, Pagination } from 'antd';
import { Link } from 'react-router-dom';

import PageHeader from '../../../component/page_header/page_header';           //
import AddGoodComponent from '../addGoods/addGoodComponent';                       //
import Type1 from './type1';                                                //taobao
import Type2 from './type2';                                                //taobao
import KeywordComponent from './keyword/keyword';                             //KeywordComponent
import { _Publishindex, _GoodsInfoList } from '../../../component/api';                        //ajax

import './release.css';

const TabPane = Tabs.TabPane;                   //Tabs
const RadioGroup = Radio.Group;                 //
const Option = Select.Option;                   //

class ReleaseTask extends Component  {
  constructor() {
    super();
    this.state = {
      spell: 100,                                   // 
      keyword: 1,                                 //
      visible: false,                              //visible true   
      addGoodsNum: false,                         // addGoodsNum true
      commodity_div: false,                       // commodity_div true 
      commodity1: 1,                              //1  
      additionalReview: false,                    // 
      publish_type: false,                        //  true
    }
  }

  componentWillMount() {
    // 
    _Publishindex()
    .then(res => {
      console.log(res.data.data);
    })
    .catch(err => {
      console.log(err);
    });
    // 
    let platform = {  // 
      platform: this.state.spell,
    }
    _GoodsInfoList(platform, 1)
    .then(res => {
      // console.log(res.data.data);
      this.setState({
        goodsLists: res.data.data.list,                                         //
        page_count: res.data.data.page_count,                                   //
      })
    })
    .catch(err => {
      console.log(err);
    })
  }

  //  pingtai
  onSpell = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      spell: e.target.value,
      commodity_div: false,
      commodity1: 1,
    });
  }
  FudianBtn = (e) => {
    console.log(e.target.value);
    if ( e.target.value === '2' ) {
      this.setState({
        additionalReview: true,
        commodity_div: false,
        publish_type: true,
        commodity1: 1,
      })
    } else if ( e.target.value === '3' ) {
      this.setState({
        publish_type: true,
        additionalReview: false,
      })
    } else {
      this.setState({
        additionalReview: false,
        publish_type: false,
        commodity_div: false,
        commodity1: 1,
      })
    }
  }

  // 
  xuanzeBtn = () => {
    this.setState({
      visible: true,
    });
  }
  // 
  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  }
  // 
  onPagination = (pageNumber) => {
    // 
    let platform = {  // 
      platform: this.state.spell,
    }
    _GoodsInfoList(platform, pageNumber)
    .then(res => {
      console.log(res.data.data);
      this.setState({
        goodsLists: res.data.data.list,                                         //
      })
    })
    .catch(err => {
      console.log(err);
    })
    console.log('Page: ', pageNumber);
  }
  // 
  goodSelectBtn = (id) => {
    console.log(id);
    let goodsLists = this.state.goodsLists;
    for (let i = 0; i < goodsLists.length; i++) {
      if ( goodsLists[i].id === id ) {
        this.setState({
          newGoodsLists: [goodsLists[i]],
        })
      }
    }
    this.setState({
      visible: false,                     //
      commodity1: 2,                      //
      commodity_div: true,                //
    });
  }
  // xinzengbaobei
  addGoodsBtn = () => {
    console.log(456);
    this.setState({
      addGoodsNum: !this.state.addGoodsNum,                     //addGoodsNum 
    })
  }

  // 
  handleSubmit = () => {
  }

  render() {
    const { spell, addGoodsNum, commodity_div, commodity1, additionalReview, goodsLists,page_count,newGoodsLists,publish_type } = this.state;
    return(
      <div>
        {/*  */}
        <PageHeader />
        <div className="rawp">
          <div className="contents release">
            <div>
              {/*  */}
              <Breadcrumb className="mbx">
                <Breadcrumb.Item>
                  <Link to="/center_new">
                    <Icon type="home" />
                  </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                  <Link to="/center_new">
                    <Icon type="user" />
                    <span>Center</span>
                  </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                  Release a task
                </Breadcrumb.Item>
              </Breadcrumb>
            </div>
            <div className="releasetop">
              <div>
                <Icon type="alert" />
                <span className="releasetop_span">Hil lkKowiwy sojlij</span>
                <span>jo</span>
              </div>
              <Button>Click to expand the previous task</Button>
            </div>
            <h1 style={{ color: '#e96262' }}>releaseTask</h1>
            <Tabs className="releaseHeader" defaultActiveKey="1">
              {/* <TabPane tab={<span className="releaseSpan"></span>} key="1">Tab 1</TabPane>
                <TabPane tab={<span className="releaseSpan"></span>} disabled key="2">Tab 2</TabPane>
              <TabPane tab={<span className="releaseSpan"></span>} disabled key="3">Tab 3</TabPane> */}
              <TabPane tab={<span className="releaseSpan">gwee</span>} key="1">
                <h2>1.Select the platform and task type</h2>
                <RadioGroup className="releaseLabel" onChange={this.onSpell} value={spell}>
                  {
                    publish_type ?
                      <div>
                        <Radio value={100}>taobao</Radio>
                        <Radio value={101}>tianmao</Radio>
                      </div>
                    :
                    <div>
                      <Radio value={100}>taobao</Radio>
                      <Radio value={101}>tianmao</Radio>
                      <Radio value={3}>jingdong</Radio>
                      <Radio value={4}>pinduoduo</Radio>
                    </div>
                  }
                </RadioGroup>
                {/*   */}
                {
                  spell === 100 || spell === 101 ?
                    <Type1 FudianBtn={this.FudianBtn} />
                  :
                  <Type2 />
                }
                <h2>2.Xuan ze goods</h2>
                <Button className="goodsBtns" onClick={ this.xuanzeBtn }>jdioowljh</Button>
                {/* //      */}
                {
                  additionalReview ?
                    ''
                  :
                  <Button className="goodsBtns" onClick={ this.addGoodsBtn }>addGoods</Button>
                }
                {/* commodity_divtrue  */}
                {
                  commodity_div ?
                    newGoodsLists.map((item, index) => {
                      return(
                        <div key={item.id} className="addGoodComponents_style">
                          <div className="commodity_div">
                            <p>
                              <img src={item.icon_img} alt='typeTu'/>
                              <span>taoba{item.store_name}</span>
                            </p>
                            <p>bianji</p>
                          </div>
                          <div className="commodity_div commodity_div1">
                            <div style={{ display: 'flex', alignItems:'center' }}>
                              <img style={{ width: '16%' }} src={item.phone_img1} alt='typeTu'/>
                              <p>{item.goods_name}</p>
                            </div>
                            <p>ds{item.pay_price}</p>
                            <p>ds{item.show_price}</p>
                            <p>ds{item.pay_nums}</p>
                            <p>ds{item.rules}</p>
                            {
                              item.free_post === 1?
                                <p>bao</p>
                              :
                              <p>sdfsw</p>
                            }
                          </div>
                        </div>
                      )
                    })
                  :
                    ""
                }

                {/* addGoodsNumtrue goods */}
                {
                  addGoodsNum ?
                    <div className="addGoodComponents_style">
                      <AddGoodComponent />
                    </div>
                  :
                    ""
                }
                {/* 3.guanjianchi */}
                {/* //    */}
                {
                  additionalReview ?
                    ''
                  :
                  <div className="typestyle">
                    <h2>3.guanjianchi fankgjioajo</h2>
                    {/*   */}
                    {
                      commodity1 === 1 ?
                        <div className="key_scheme">
                          <span></span>
                          <span>APP</span>
                          <span>PC</span>
                          <span></span>
                          <span></span>
                          <span></span>
                        </div>
                      :
                      <KeywordComponent />
                    }
                  </div>
                }
              </TabPane>
              <TabPane tab={<span className="releaseSpan">yjk7y</span>} disabled key="2">Tab 2</TabPane>
              <TabPane tab={<span className="releaseSpan">ykll,</span>} disabled key="3">Tab 3</TabPane>
            </Tabs>
            <div style={{ padding: '50px 0' }} className="releaseBtnok">
              <Button></Button>
              <Button onClick={this.handleSubmit} type="primary"></Button>
            </div>
          </div>
        </div>

        {/*  */}
        <Modal
          title="Basic Modal"
          visible={this.state.visible}
          footer={null}
          onCancel={this.handleCancel}
          wrapClassName="modal_Width"
          style={{ width: '70%' }}
        >
          <div className="yajin_center_tab">
            <div style={{ width: '30%' }}>
              <span>dsfds</span>
              <Select style={{ width: '75%' }} placeholder="">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </div>
            <p style={{ width: '30%' }}>
              <span>itme</span>
              <Input style={{ width: '20%', marginRight: '5px' }} />
              <span></span>
              <Input style={{ width: '20%', margin: '0 5px' }} />
              <span></span>
            </p>
            <p style={{ width: '40%', marginRight: '0' }}>
              <Input placeholder="" style={{ marginRight: '15px' }} />
              <Button></Button>
            </p>
          </div>
          {/* goods */}
          <div>
            <div className="goodsKu">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
            {/* div */}
            {
              goodsLists ?
                goodsLists.map((item, index) => {
                  return(
                    <div key={index} className="goodsKu_child">
                      <p>
                        <img src={item.icon_img} alt="types"/>
                        <span>{item.store_name}</span>
                      </p>
                      <div className="goodsKu_bottom">
                        <img src={item.phone_img1} alt="" />
                        <span>{item.goods_name}</span>
                        <span>{item.pay_price}</span>
                        <span>{item.rules}</span>
                        <span>{item.use_last_time}</span>
                        <Button onClick={()=>this.goodSelectBtn(item.id) } type="primary"></Button>
                      </div>
                    </div>
                  )
                })
              :
              ''
            }
            <Pagination className="paginations" defaultPageSize={3} showQuickJumper defaultCurrent={1} total={page_count} onChange={this.onPagination} />
          </div>
        </Modal>
      </div>
    )
  }
}

export default ReleaseTask
