import React, { Component } from 'react';
import { Form, Input, Tabs ,Button, Checkbox, Icon, message } from 'antd';
import { Link } from 'react-router-dom';

import CodeLogin from './codeLogin';          //
import { _login } from '../../component/api'; //ajax
import './login.css';

const publics = require('../../component/publics'); //
const phoneNum = publics.phoneNum; //

// tabs
const TabPane = Tabs.TabPane;
// var cookie = document.cookie;                  //cookie
// console.log(cookie);

class Logins extends Component {
  constructor() {
    super();
    this.state = {
    }
  }

  componentDidMount () {
      this.props.form.setFieldsValue({
      mobile: localStorage.getItem("mobile"),        //
      pwd: localStorage.getItem("pwd"),              //
    })
  }

  // 
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        // console.log(values);
        if ( !phoneNum.test(values.mobile)) {
          message.error("")
        } else {
          // ajax
          _login(values)
          .then(res => {
            // console.log(res.data);
            if ( res.data.code === 200 ) {
              message.success(res.data.msg);
                // token
                localStorage.setItem("tokens", res.data.data.token);
              if ( values.remember ) {                                      //
                localStorage.setItem("mobile", values.mobile);              //
                localStorage.setItem("pwd", values.pwd);               //
              } else {                                                      //
                localStorage.removeItem("mobile");                          //
                localStorage.removeItem("pwd");
              }
              // 
              this.props.history.push('../center_new/center_new');
            } else {
              message.warning(res.data.msg)
            }
          })
          .catch(err => {
            console.log(err);
          })
          // key
          localStorage.removeItem("key")
        }
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return(
      <div className="login">
        {/* header */}
        <div className="login-header">
          <div className="login-header-left">
            <img src={require("../../imgs/logo.png")} alt="logo"/>
            <span></span>
          </div>
          <div className="login-header-right">
            <span></span>
            <Link to="/register"></Link>
          </div>
        </div>
        {/* content */}
        <div className="login-content">
          {/*  */}
          <div className="content-left">
            {/* <img src={require("../../imgs/logo.png")} alt="logo"/> */}
          </div>
          {/*  */}
          <div className="content-right">
            <Tabs defaultActiveKey="1" onChange={this.callback}>
              {/*  */}
              <TabPane tab="" key="1">
                <Form onSubmit={this.handleSubmit} className="login-form loginPage">
                  <Form.Item>
                    {getFieldDecorator('mobile', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input maxLength={11} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="\" />
                    )}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator('pwd', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
                    )}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator('remember', {
                      valuePropName: 'checked',
                      initialValue: true,
                    })(
                      <Checkbox></Checkbox>
                    )}
                    <Link className="login-form-forgot" to="/find_password"></Link>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                      
                    </Button>
                     <Link to="/register">!</Link>
                  </Form.Item>
                </Form>
              </TabPane>
              {/*  */}
              <TabPane tab="" key="2">
                <CodeLogin />
              </TabPane>
            </Tabs>
          </div>
        </div>
      </div>
    )
  }
}

const Login = Form.create()(Logins);
export default Login
